import './AdminDashboard.css'

import { Capacitor } from '@capacitor/core'
import { ResponsiveBar } from '@nivo/bar'
import { ResponsivePie } from '@nivo/pie'
import { BasicTooltip } from '@nivo/tooltip'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { useHistory } from 'react-router'

import { Page } from '../../components'
import { RUTAS, RUTAS_APP_FABRICANTE } from '../../constants'
import { cambiarModo, useOpenPage } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'

const { ID_ONE_SIGNAL } = require('../../config/env')

type HomeProps = {}

export const AdminDashboard: React.FC<HomeProps> = observer(() => {
  const rootStore = useStores()
  const openPage = useOpenPage()
  const history = useHistory()
  const { adminStore, loginStore } = rootStore
  const [ProductosDisponibles, setProductosDisponibles] = useState(0)
  const [PedidosPendientes, setPedidosPendientes] = useState(0)
  const [PedidosRetirar, setPedidosRetirar] = useState(0)
  const [ProductosGrafico, setProductosGrafico] = useState<any[]>([])
  const [ProductosGraficoProcentaje, setProductosGraficoProcentaje] = useState<any[]>([])
  const [ProductosGraficoBarra, setProductosGraficoBarra] = useState<any[]>([])
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)

  React.useEffect(() => {
    if (!rootStore.loginStore.isLoggedIn) {
      document.location.href = RUTAS.ROOT
    } else if (Movil && rootStore.loginStore.fabricante) {
      document.location.href = RUTAS_APP_FABRICANTE.HOME
    }
  }, [])

  const setup = async () => {
    if (loginStore.fabricante_id) {
      obtenerFabricante(loginStore.fabricante_id)
      if (loginStore.modo_uso_app === 20) {
        if (ID_ONE_SIGNAL && Capacitor.getPlatform() === 'web') {
          const OneSignal = (window as any).OneSignal || []
          OneSignal.push(function () {
            OneSignal.getUserId(function (ids: any) {
              if (ids) {
                loginStore.environment.api.setDevice(ids, '', true).then((result) => {
                  console.log('Datos de onesignal enviados al backend', result)
                })
              } else {
                OneSignal.init({
                  appId: ID_ONE_SIGNAL,
                  autoRegister: true,
                  notifyButton: {
                    enable: true,
                  },
                })
                OneSignal.getSubscription(function (notOptedOut: any) {
                  if (notOptedOut) {
                    OneSignal.registerForPushNotifications()
                  }
                })
              }
            })
          })
          OneSignal.push([
            'addListenerForNotificationOpened',
            function (event: any) {
              if ('chat' in event.data) {
                openPage(RUTAS.ADMIN_PEDIDO_DETAIL(event.data.pedido_id), true)
              } else if ('pedidos' in event.data) {
                openPage(RUTAS.ADMIN_ORDEN_VENTA)
              }
            },
          ])
        }
      }
    }
    obtenerCiudades()
    obtenerTiposVehiculos()
    obtenerDatosInicio()
    if (loginStore.administrador) {
      obtenerConfiguraciones()
    }
  }

  useEffect(() => {
    setup()
  }, [])

  const obtenerFabricante = (fabricante_id: number) => {
    // @ts-ignore
    rootStore.environment.api.getFabricante(fabricante_id).then((result) => {
      if (result.kind === 'ok') {
        adminStore.setFabricanteDetalle(result.fabricante)
        //
        if (result.fabricante.bloqueado) {
          alert('Su perfil de proveedor se encuentra deshabilitado, por favor contacte con la administración')
          // Cambiar a modo cliente
          cambiarModo(rootStore, history, 10)
        }
      }
    })
  }

  const obtenerCiudades = () => {
    if (!rootStore.ciudades.length) {
      rootStore.actualizarDatosInicio(false)
    }
  }

  const PieTooltip = function PieTooltip(_ref: any) {
    var datum = _ref.datum
    return React.createElement(BasicTooltip, {
      id: datum.label,
      value: datum.formattedValue,
      enableChip: true,
      color: datum.color,
    })
  }

  const obtenerTiposVehiculos = () => {
    rootStore.environment.api.tiposVehiculos().then((result) => {
      if (result.kind === 'ok') {
        // @ts-ignore
        rootStore.setTiposVehiculos(result.data.results)
      }
    })
  }

  const obtenerConfiguraciones = () => {
    rootStore.environment.api.configuraciones().then((result) => {
      if (result.kind === 'ok') {
        // @ts-ignore
        rootStore.setConfiguraciones(result.configuraciones)
      }
    })
  }

  const obtenerDatosInicio = () => {
    rootStore.environment.api.inicioDashboard().then((result) => {
      if (result.kind === 'ok') {
        setProductosDisponibles(result.data.productos_disponibles_cantidad)
        setPedidosPendientes(result.data.ordenes_pendientes_cantidad)
        setPedidosRetirar(result.data.ordenes_retirar_cantidad)
        setProductosGrafico(result.data.productos_grafico)
        setProductosGraficoProcentaje(result.data.productos_grafico_porcentaje)
        setProductosGraficoBarra(result.data.productos_grafico_barra)
      }
    })
  }

  return (
    <Page login_required admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className={'row'}>
            <div
              style={{ cursor: 'pointer' }}
              className={'col-12 col-lg-4'}
              onClick={openPage(RUTAS.ADMIN_PRODUCTO('moderado'))}
            >
              <div className={'card card-body'}>
                <h3>Productos disponibles</h3>
                <div className={'d-flex mt-3'}>
                  <FIcon.Package color={'#303AB2'} size={30} />
                  <h2 className={'ml-auto'}> {ProductosDisponibles}</h2>
                </div>
              </div>
            </div>
            <div
              style={{ cursor: 'pointer' }}
              className={'col-12 col-lg-4'}
              onClick={
                // @ts-ignore
                () => history.push({ pathname: RUTAS.ADMIN_ORDEN_VENTA, estado: 'pendientes' })
              }
            >
              <div className={'card card-body'}>
                <h3>Pedidos pendientes</h3>
                <div className={'d-flex mt-3'}>
                  <FIcon.Box color={'#303AB2'} size={30} />
                  <h2 className={'ml-auto'}> {PedidosPendientes}</h2>
                </div>
              </div>
            </div>
            <div
              style={{ cursor: 'pointer' }}
              className={'col-12 col-lg-4'}
              onClick={openPage(RUTAS.ADMIN_ORDEN_VENTA)}
            >
              <div className={'card card-body'}>
                <h3>Pedidos a retirar</h3>
                <div className={'d-flex mt-3'}>
                  <FIcon.CreditCard color={'#303AB2'} size={30} />
                  <h2 className={'ml-auto'}> {PedidosRetirar}</h2>
                </div>
              </div>
            </div>
          </div>
          <div className={'row'}>
            <div className={'col-12 col-lg-6'}>
              <h3>Ventas últimos 3 meses</h3>
            </div>
            <div className={'col-12 col-lg-6'}>
              <h3>Ventas últimos 6 meses</h3>
            </div>
            <div className={'col-12 col-lg-6'} style={{ minHeight: 600 }}>
              <ResponsivePie
                data={ProductosGraficoProcentaje}
                margin={{ top: 40, right: 80, bottom: 80, left: 80 }}
                innerRadius={0.5}
                padAngle={0.7}
                cornerRadius={3}
                colors={{ scheme: 'paired' }}
                borderWidth={1}
                borderColor={{ from: 'color', modifiers: [['darker', 0.2]] }}
                arcLabelsSkipAngle={10}
                arcLabelsTextColor="#333333"
                arcLinkLabelsColor={{ from: 'color' }}
                tooltip={PieTooltip}
              />
            </div>
            <div className={'col-12 col-lg-6'}>
              <ResponsiveBar
                data={ProductosGraficoBarra}
                keys={['value']}
                indexBy="mes"
                groupMode="grouped"
                margin={{ top: 50, right: 130, bottom: 50, left: 60 }}
                padding={0.3}
                valueScale={{ type: 'linear' }}
                indexScale={{ type: 'band', round: true }}
                colors={['#303AB2']}
                axisTop={null}
                axisRight={null}
                axisBottom={{
                  tickSize: 5,
                  tickPadding: 5,
                  tickRotation: 0,
                  legend: 'Meses (miles de guaraníes)',
                  legendPosition: 'middle',
                  legendOffset: 32,
                }}
                axisLeft={{
                  tickSize: 5,
                  tickPadding: 5,
                  tickRotation: 0,
                  legend: 'Ventas (millones de guaraníes)',
                  legendPosition: 'middle',
                  legendOffset: -55,
                }}
                labelTextColor="white"
                labelSkipWidth={12}
                labelSkipHeight={12}
                animate={true}
              />
            </div>
            <div className={'row'}>
              <div className={'col-12'}>
                <h3>Productos disponibles</h3>
              </div>
              <div className={'col-12'} style={{ minHeight: 600 }}>
                <ResponsivePie
                  data={ProductosGrafico}
                  margin={{ top: 40, right: 80, bottom: 80, left: 80 }}
                  innerRadius={0.5}
                  padAngle={0.7}
                  cornerRadius={3}
                  colors={{ scheme: 'nivo' }}
                  borderWidth={1}
                  borderColor={{ from: 'color', modifiers: [['darker', 0.2]] }}
                  arcLabelsSkipAngle={10}
                  arcLabelsTextColor="#333333"
                  tooltip={PieTooltip}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminDashboard
